import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import api from "../utils/api";

const initialState = {
  name: "",
  email: "",
  connectedSM: {},
  posts: [],
};

export const getUserData = createAsyncThunk("user/getData", async () => {
  const userData = await api
    .getUserInfo()
    .then((data) => data)
    .catch((error) => {
      return Promise.reject("Cannot get user data");
    });

  const userPosts = await api
    .getposts()
    .then((data) => {
      console.log(data);
    })
    .catch((error) => {
      return Promise.reject("Cannot get user posts");
    });
  return { userData, userPosts };
});

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(getUserData.fulfilled, (state, action) => {
      const { name, email, connectedSM } = action.payload.userData;
      const posts = action.payload.userPosts;

      state.name = name;
      state.email = email;
      state.connectedSM = connectedSM;
      state.posts = posts;
    });
    builder.addCase(getUserData.rejected, (state, action) => {
      console.error(action.error.message);
    });
  },
});

// export const {} = userSlice.actions;
export default userSlice.reducer;
